<Teleport to="body"><div class="semi-transparent" :class="{'hide': !props.data.display}"><div class="modal"><div class="modal__title">{{ props.data.title }}</div><div class="modal__body" :class="{ imgTick: !props.data.msg }">{{ props.data.msg }}</div><div class="modal__footer"><button @click="close">{{ props.data.btnText }}</button></div></div></div></Teleport>