<div class="roomGrid"><div class="room" v-for="room in rooms" :key="room.id" @click="showDetail(room.id)" @keyup="showDetail(room.id)"><div class="room-img" :style="bgImage(room.imageUrl)"></div><ul class="room-info"><li class="room-info__name-eng">{{ room.name }}</li><li class="room-info__name-chi">{{ roomNameChi(room.name) }}</li><li class="room-info__price">NT.{{ room.normalDayPrice }}&nbsp;<span class="price-weekday">平日</span><span class="price-holiday">NT.{{ room.holidayPrice }} 假日</span></li></ul></div></div>